import { render } from "@testing-library/react";
import OrderPriceBlock from "./OrderPriceBlock";
import '../CSS/OrderBook.css';

function OrderBook({info}){

    if(info.orderBook)
    {   //set bid
        const bidBlock = info.orderBook.orderbook_units.map((item)=>{
            return { type:"bid", price: item.bid_price, size: item.bid_size }
        })
        const askBlock = info.orderBook.orderbook_units.map((item)=>{
            return { type:"ask", price: item.ask_price, size: item.ask_size }
        }).reverse()
        
        return(
            <div className="OrderBook">
                <div className="OrderPriceBlockDiscription">
                    <p>매도</p>
                    <p>호가</p>
                    <p>매수</p>
                </div>
                {askBlock.map((item,idx)=><OrderPriceBlock props={item} key={idx} />)}
                {bidBlock.map((item,idx)=><OrderPriceBlock props={item} key={idx} />)}
            </div>
        )
    }
    
    return(
        <div className="OrderBook">
            Waiting...
        </div>
    )
}

export default OrderBook;