import { useEffect } from "react";
import {
    HashRouter as Router, Route, Switch,
    // Switch,
    // Route,
    // Link
} from "react-router-dom";
import { useCoinDispatch, useCoinState } from "../Context/CoinContext";
import { getMarkets, getRealTimeData } from "../Controller/CoinController";
import Navigation from "./Navigation";

import '../CSS/CoinTrade.css';
import Home from "./Home";
import Order from "./Order";
import ChartPage from "./ChartPage";
import About from "./About";

function CoinTrade() {
    const coinState = useCoinState();
    const coinDispatch = useCoinDispatch();

    //Markets목록 가져오기
    useEffect(()=>{
        coinDispatch({
            type:"GET_MARKETS"
        })
        let socket;
        try{
            getMarkets()
            .then((markets)=>{
                coinDispatch({
                    type: "GET_MARKETS_SUC",
                    markets
                })
                return markets
            })
            .then((markets)=>{
                coinDispatch({
                    type: "UPDATE_MARKET"
                })
                getRealTimeData(socket, markets, coinState, coinDispatch)
            })
        } catch(error){
            console.log(error)
            coinDispatch({
                type: "GET_MARKETS_FAIL"
            })
        }
        return(()=>{
            if(socket) socket.close()
        })
    },[coinDispatch])

    return(
        <div className="CoinTrade">
            <div className="Screen" ></div>
            <Router>
                <Navigation/>
                <div className="contents">
                    <Switch>
                        <Route path='/' exact component={Home} />
                        <Route path='/order/:id' exact component={Order} />
                        <Route path='/chart/:id' exact component={ChartPage} />
                        <Route path='/setting' exact component={Home} />
                        <Route path='/about' exact component={About} />
                        <Route path='/*' exact>
                            Wrong page
                        </Route>
                    </Switch>
                </div>
            </Router>
        </div>
    )
}

export default CoinTrade;
