import { getCommaNumber } from "../Controller/CoinController";

function OrderPriceBlock({props}) {
    const { type, price, size} = props

    if(type==="bid")
    {
        return(
                <div className="OrderPriceBlock" id={type}>
                    <div className="askSize">
                    </div>
                    <div className="price">
                        {getCommaNumber(price)}
                    </div>
                    <div className="bidSize">
                        {size.toFixed(2)}
                    </div>
                </div>
        )
    }
    else{
        return(
            <div className="OrderPriceBlock" id={type}>
                <div className="askSize">
                    {size.toFixed(2)}
                </div>
                <div className="price">
                    {getCommaNumber(price)}
                </div>
                <div className="bidSize">
                </div>
            </div>
        )
    }
}

export default OrderPriceBlock;