import React, {useContext, useReducer} from 'react'
import {reducer} from '../Reducer/AccountReducer';

export const AccountStateContext = React.createContext(null);
export const AccountDispatchContext = React.createContext(null);

export const initialState = {
    money: '10000'
}

export function AccountProvider({ children }) {
    const [state, dispatch] = useReducer(reducer, initialState);
    return (
      <AccountStateContext.Provider value={state}>
        <AccountDispatchContext.Provider value={dispatch}>
          {children}
        </AccountDispatchContext.Provider>
      </AccountStateContext.Provider>
    );
}

export const useAccountState = () => {
    const state = useContext(AccountStateContext);
    if(!state){
        throw new Error("AState not found")
    }
    return state
}

export const useAccountDispatch = () => {
    const dispatch = useContext(AccountDispatchContext);
    if(!dispatch){
        throw new Error('Adispatch not found')
    }
    return dispatch
}
