import { useCoinState } from "../Context/CoinContext";
import CoinBlock from "./CoinBlock";

function CoinBlockList({sortBy}) {
    const coinState = useCoinState();
    let showMarkets = []

    return(
        <div className="CoinBlockList">
            {sortMarkets(sortBy, coinState).map((item,index)=><CoinBlock key={index} info={item}/>)}
        </div>
    )
}

export default CoinBlockList;

const sortMarkets = (sortBy, coinState) =>{
    let showMarkets = []
    if(sortBy==="trade_price"){
        showMarkets = coinState.markets.sort((a, b)=>{
            return b.acc_trade_price_24h - a.acc_trade_price_24h;
        }).slice(0,5)
    }
    else if(sortBy==="RISE"){
        showMarkets = coinState.markets.sort((a, b)=>{
            let acomb = a.change_rate
            let bcomb = b.change_rate
            if(a.change==="FALL") acomb = -acomb;
            if(b.change==="FALL") bcomb = -bcomb;
            return bcomb - acomb;
        }).slice(0,5)
    }
    else if(sortBy==="FALL"){
        showMarkets = coinState.markets.sort((a, b)=>{
            let acomb = a.change_rate
            let bcomb = b.change_rate
            if(a.change==="FALL") acomb = -acomb;
            if(b.change==="FALL") bcomb = -bcomb;
            return acomb - bcomb;
        }).slice(0,5)
    }
    else{
        showMarkets=coinState.markets
    }

    return showMarkets
}
