import { useParams } from "react-router-dom";
import { useCoinDispatch, useCoinState } from "../Context/CoinContext";
import CoinDetail from "../Components/CoinDetail";
import OrderBook from "./OrderBook";
import { useEffect, useRef } from "react";
import { getDetailData } from "../Controller/CoinController";

function Order() {
  const coinState = useCoinState();
  const coinDispatch = useCoinDispatch();
  const params = useParams().id;

  const market = coinState.markets.find(
    (item) => item.market.split("-")[1] === params
  );
  const loaded = market && market.trade_price && true;

  useEffect(() => {
    console.log(market);
    if (market) {
      getDetailData.open(market.market, coinDispatch);
    }
    return () => {
      getDetailData.close();
    };
  }, [params, loaded]);

  return (
    <div className="Order">
      <h1>Order</h1>
      {market === undefined && <h2>Not Found Coin {params}</h2>}
      {market !== undefined && market.loaded && (
        <>
          <CoinDetail info={market} />
          <OrderBook info={market} />
        </>
      )}
    </div>
  );
}

export default Order;
