import axios from 'axios';

//Get market list
export const getMarkets = async () => {
    const rawMarkets = await axios.get('https://api.upbit.com/v1/market/all?isDetails=true')
    let markets = rawMarkets.data.filter((item)=>{
        return item.market.includes('KRW-')
    })
    return markets.map(item => {return {...item, loaded:false}})
}

//Get market's basic value
export const getRealTimeData = async (socket, markets, coinState, coinDispatch) => {
    socket = new WebSocket('wss://api.upbit.com/websocket/v1');
    let marketsName = markets.map((item) => `"${item.market}"`)
    
    socket.onopen = () =>{
        // const message = 
        // `[{"ticket":"test"},{"type":"trade","codes":[${marketsName}]},{"type":"orderbook","codes":[${marketsName}]},{"type":"ticker","codes":[${marketsName}]}]`;
        
        // const message = 
        // `[{"ticket":"asdlkfjasdljk"},{"type":"trade","codes":[${marketName}]},{"type":"orderbook","codes":[${marketName}]}]`;


        const message = 
        `[{"ticket":"test"},{"type":"ticker","codes":[${marketsName}]}]`;


        socket.send(message);
    }
    
    socket.onmessage = async (message)=>{
        const text = await new Response(message.data).text();
        const realTimeData = JSON.parse(text)
        
        // console.log(realTimeData)

        if(realTimeData.type==="ticker") {
            coinDispatch({
                type: "UPDATE_MARKET",
                realTimeData
            })
        }
    }
}

export const getDetailData = (() =>{
    let socket;
    return{
        open:(market, coinDispatch)=>{
            
            try{
                socket = new WebSocket('wss://api.upbit.com/websocket/v1');
                
                socket.onopen = ()=>{
                    const message = 
                    `[{"ticket":"asdlkfjasdljk"},{"type":"trade","codes":["${market}"]},{"type":"orderbook","codes":["${market}"]}]`;
                    
                    socket.send(message);
                }

                socket.onmessage = async (message)=>{
                    const text = await new Response(message.data).text();
                    const realTimeData = JSON.parse(text)
            
                    console.log(realTimeData)
                    if(realTimeData.type==="orderbook") {
                        coinDispatch({
                            type: "UPDATE_ORDERBOOK",
                            realTimeData
                        })
                    }
                    if(realTimeData.type==="trade"){
                        coinDispatch({
                            type: "UPDATE_TRADE",
                            realTimeData
                        })
                    }
                }
            } catch(error){
                console.log(error)
            }
        },
        close:()=>{
            if(socket) socket.close();
        }
    }
})();

export const getCommaNumber = (num) =>{
    if(!num) return ''
    return num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");   
}

export const getChangeRate = (num) => {
    if(!num) return ''
    return (100*num).toFixed(2);
}
