import { useEffect, useRef } from 'react';
import { getChangeRate, getCommaNumber } from '../Controller/CoinController';
import '../CSS/CoinDetail.css'

function CoinDetail({info}) {
    const block = useRef();

    useEffect(()=>{

    })

    return(
        <div className="CoinDetail">
            <div className={`CoinDetail${info.change}`} ref={block} id="cd">
                <div className="content">
                    <div className="mainContent">
                        <img className="coinImg" src={`https://static.upbit.com/logos/${info.market.split('-')[1]}.png`}/>
                        <span className="koreanName">
                            {info.korean_name}
                        </span> 
                        <span className="englishName">
                            {info.market.split('-')[1]}
                        </span>
                        <span className="englishFullName">
                            {info.english_name}
                        </span>
                        <span className="tradePrice">
                            {getCommaNumber(info.trade_price)}
                        </span>
                        <span className="changePrice">
                            {info.change==="RISE"? "▲":""}
                            {info.change==="FALL"? "▼":""}
                            {info.change==="EVEN"? "0":""}
                            {getCommaNumber(info.change_price)}
                        </span>
                        <span className="changePercent">
                            {info.change==="RISE"? "+":""}
                            {info.change==="FALL"? "-":""}
                            {getChangeRate(info.change_rate)}%
                        </span>
                    </div>
                    <div className="detailContent">
                        <span className="detailTitle">
                            OPEN
                        </span>
                        <span className="detailDescription">
                            {info.opening_price}
                        </span>
                        <span className="detailTitle">
                            PREV CLOSE
                        </span>
                        <span className="detailDescription">
                            {info.prev_closing_price}
                        </span>
                        <span className="detailTitle">
                            HIGH
                        </span>
                        <span className="detailDescription">
                            {info.high_price}
                        </span>
                        <span className="detailTitle">
                            LOW
                        </span>
                        <span className="detailDescription">
                            {info.low_price}
                        </span>
                        <span className="detailTitle">
                            24H VOLUME(WON)
                        </span>
                        <span className="detailDescription">
                            {info.acc_trade_price.toFixed(2)}
                        </span>
                        <span className="detailTitle">
                            24H VOLUME(EA)
                        </span>
                        <span className="detailDescription">
                            {info.acc_trade_volume.toFixed(2)}
                        </span>
                        <span className="detailTitle">
                            52 WEEK HIGH
                        </span>
                        <span className="detailDescription">
                            {info.highest_52_week_price}
                        </span>
                        <span className="detailTitle">
                            52 WEEK LOW
                        </span>
                        <span className="detailDescription">
                            {info.lowest_52_week_price}
                        </span>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default CoinDetail;
