import { act } from "@testing-library/react";

export function reducer(state, action) {
    switch(action.type){
        case 'GET_MARKETS':
            return{
                ...state,
                marketsLoaded:false
            }
        case 'GET_MARKETS_SUC':
            return{
                ...state,
                marketsLoaded:true,
                markets: action.markets,
                marketsList: action.markets
            }
        case 'GET_MARKETS_FAIL':
            return{
                ...state,
                marketsLoaded:false
            }
        case 'UPDATE_MARKET':
            if(!action.realTimeData){
                return state;
            }
            return{
                ...state,
                markets: state.markets.map((item)=>{
                    if(item.market === action.realTimeData.code){
                        return {...item, ...action.realTimeData, loaded:true}
                    }
                    return item
                })
            }
        case 'UPDATE_ORDERBOOK':
            if(!action.realTimeData){
                return state;
            }
            return{
                ...state,
                markets: state.markets.map((item)=>{
                    if(item.market === action.realTimeData.code){
                        return {...item, orderBook: action.realTimeData}
                    }
                    return item
                })
            }
        case 'UPDATE_TRADE':
            if(!action.realTimeData){
                return state;
            }
            return{
                ...state,
                markets: state.markets.map((item)=>{
                    if(item.market === action.realTimeData.code){
                        return {...item, trade: action.realTimeData}
                    }
                    return item
                })
            }
        default:    
            return{
                ...state,
                markets: 'test'
            }
    }
}
