import React, {useContext, useReducer} from 'react'
import {reducer} from '../Reducer/CoinReducer';

export const CoinStateContext = React.createContext(null);
export const CoinDispatchContext = React.createContext(null);

export const initialState = {
    marketsLoaded: false,
    markets: [],
    nowLooking: ''
}

export function CoinProvider({ children }) {
    const [state, dispatch] = useReducer(reducer, initialState);
    return (
      <CoinStateContext.Provider value={state}>
        <CoinDispatchContext.Provider value={dispatch}>
          {children}
        </CoinDispatchContext.Provider>
      </CoinStateContext.Provider>
    );
}

export const useCoinState = () => {
    const state = useContext(CoinStateContext);
    if(!state){
        throw new Error("State not found")
    }
    return state
}

export const useCoinDispatch = () => {
    const dispatch = useContext(CoinDispatchContext);
    if(!dispatch){
        throw new Error('dispatch not found')
    }
    return dispatch
}
