import Axios from "axios";
import { useEffect, useState } from "react";
import { Line } from "react-chartjs-2";
import { useParams } from "react-router-dom";
import { useCoinDispatch, useCoinState } from "../Context/CoinContext";
import format from "date-format";

function ChartPage() {
  const [chartData, setChartData] = useState({});
  const [lastDataTime, setLastDataTime] = useState([1, 0]);

  const coinState = useCoinState();
  const coinDispatch = useCoinDispatch();

  const params = useParams().id;

  const market = coinState.markets.find(
    (item) => item.market.split("-")[1] === params
  );
  const loaded = market && market.trade_price && true;

  const option = {
    responsive: true,
    maintainAspectRatio: false,
    animation: {
      duration: 500,
    },
  };

  const getChartData = async () => {
    const response = await Axios.get(
      `https://api.upbit.com/v1/candles/minutes/60?market=KRW-${params}&count=200`
    );
    const data = response.data
      .map((item) => {
        return {
          x: format.asString("dd / hh:mm", new Date(item.timestamp)),
          y: item.trade_price,
        };
      })
      .reverse();
    const xData = data.map((item) => item.x);
    const yData = data.map((item) => item.y);
    const yesterDay = Array(200).fill(market.prev_closing_price);
    setChartData({
      labels: xData,
      datasets: [
        {
          label: params,
          borderColor: "rgba(75, 192, 192, 1)",
          fill: false,
          pointRadius: 0,
          lineTension: 0,
          data: yData,
          borderWidth: 2,
        },
        {
          label: "Previous Close",
          data: yesterDay,
          pointRadius: 0,
          borderWidth: 2,
          pointHitRadius: 0,
          fill: false,
          borderColor: "rgba(200, 1, 0, 1)",
        },
      ],
    });
    console.log(chartData);
  };

  useEffect(() => {
    if (market) getChartData();
  }, [params, loaded]);

  return (
    <div className="ChartPage">
      <h1>Chart - {params}</h1>
      {market === undefined && <h2>Not Found Coin {params}</h2>}
      {market && (
        <div className="ChartContainer">
          <Line data={chartData} options={option} height={400} />
        </div>
      )}
    </div>
  );
}

export default ChartPage;
