import { useEffect, useRef, useState } from "react";
import { getChangeRate, getCommaNumber } from "../Controller/CoinController";
import "../CSS/CoinBlock.css";

function CoinBlock({ info }) {
  const block = useRef();
  const [firstLoad, setFirstLoad] = useState(true);
  useEffect(() => {
    if (!firstLoad) {
      block.current.classList.add("refreshed");
      setTimeout(() => {
        if (block.current) block.current.classList.remove("refreshed");
      }, 1000);
    } else setFirstLoad(false);
  }, [info.trade_price]);

  return (
    <div className="CoinBlock">
      <div className={`CoinBlock${info.change}`} ref={block} id="cb">
        <div className="content">
          <img
            className="coinImg"
            src={`https://static.upbit.com/logos/${
              info.market.split("-")[1]
            }.png`}
          />
          <span className="koreanName">{info.korean_name}</span>
          <span className="englishName">{info.market.split("-")[1]}</span>
          <div>
            <span className="tradePrice">
              {getCommaNumber(info.trade_price)}
            </span>
            <div>
              <span className="changePrice">
                {info.change === "RISE" ? "▲" : ""}
                {info.change === "FALL" ? "▼" : ""}
                {info.change === "EVEN" ? "0" : ""}
                {getCommaNumber(info.change_price)}
              </span>
              <span className="changePercent">
                {info.change === "RISE" ? "+" : ""}
                {info.change === "FALL" ? "-" : ""}
                {getChangeRate(info.change_rate)}%
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default CoinBlock;
